import React, {Component} from 'react';
import {Grid, Cell} from 'react-mdl';
import Education from './education';
import Experience from './experience';
import Skills from './skills';

class Resume extends Component{
	render(){
		const leftLineStyle={
			borderTop: '3px solid #1D2671', 
			width:'50%'
		}
		const resumeLineStyle={
			borderTop: '3px solid #E6DADA'
		}
		return(
			<div className="resume-container">
			<Grid>
				<Cell col={4}>
					<div style={{textAlign: 'center'}}>
						<img
							src="https://www.shareicon.net/download/2015/09/18/103157_man_512x512.png"
							alt="avatar"
							style={{height: '200px',paddingTop:'60px', paddingRight: '200px'}}
							/>
					</div>
					<h2 style={{paddingTop: '2em'}}> Robert Martinez </h2>	
					<h4 style={{color: 'grey'}}> Software Engineer </h4>
					<hr style={leftLineStyle}/>
					<p>
						I am a Computer Engineer that is passionate about learning new technologies from 
						a variety of diciplines. My interests range from Artificial Intelligence to 
						Systems Administation, to FullStack development.	
					</p>
					<hr style={leftLineStyle}/>
					<h5> Address </h5>
					<p> 950 w. Linden st. Riverside, CA 92507 </p>
					<h5> Phone </h5>
					<p> (909) 273-8585 </p>
					<h5> Web</h5>
					<p> www.ticosite.com</p>
					<hr style={leftLineStyle}/>

				</Cell>
				<Cell className="resume-right-col" col={8}>

					<h2> Education </h2>	
					<Education
						startYear="2015"
						endYear="2018"
						schoolName="University of Riverside California"
						degree="B.S. Computer Engineering"
						schoolDescription=""
						gpa="3.28"

						/>
					<Education
						startYear="2012"
						endYear="2015"
						schoolName="Victor Valley Community College"
						degree="A.S. Math/Science"
						schoolDescription=""
						gpa="3.7"
						/>

					<hr style={resumeLineStyle} />

					<h2> Experience </h2>
					<Experience
					startYear="2016"
					endYear="2017"
					jobName="Embedded Systems Laboratory UCR"
					jobDescription="Aided graduate researchers in BioScript language"
						/>

					<hr style={resumeLineStyle} />

					<h2> Skills </h2>
					<Skills 
						skill="Python"
						progress={85}
						/>
					<Skills 
						skill="Java"
						progress={65}
						/>
					<Skills 
						skill="C++"
						progress={50}
						/>
					<Skills 
						skill="Machine Learning"
						progress={79}
						/>

				</Cell>
			</Grid>
			</div>	
			);

	}
}
export default Resume;


